export function counter () {
  const counters = document.querySelectorAll('.js__counter span') as NodeListOf<HTMLElement>;
  const speed = 1000;

  counters.forEach((counter: HTMLElement) => {
    const animate = () => {
      const value = Number(counter.dataset.number);
      const data = Number(counter.innerText);
      
      const time = value / speed;
      if(data < value) {
        counter.innerText = Math.ceil(data + time).toString();
        setTimeout(animate, 50);
      } else{
        counter.innerText = value.toString();
      }
    }
    
    animate();
  });
};
