export class Burger {
  constructor() {
    this.burger = document.querySelector('.js__burger');
    this.navigationContainer = document.querySelector('.js__navigation-container');

    this.burgerActiveClass = 'is-active';
    this.navigationContainerActiveClass = 'is-active';

    this.isOpened = false;

    this.init();
  }

  init() {
    this.burger.addEventListener('click', this.toogleNavigationContainer.bind(this));
  }

  toogleNavigationContainer() {
    if(!this.isOpened) {
      this.navigationContainer.classList.add(this.navigationContainerActiveClass);
      this.burger.classList.add(this.burgerActiveClass);
    } else {
      console.log('remove')
      this.navigationContainer.classList.remove(this.navigationContainerActiveClass);
      this.burger.classList.remove(this.burgerActiveClass);
    }
    this.isOpened = !this.isOpened;
  }
}
